<template>
    <div class="wrapper">
            <img referrerpolicy="no-referrer" :src=objResult.imgUrl alt="">
            <div class="right">
                  <div class="title">
                    <h3>{{objResult.type}}</h3>
                    <a @click="jump(objResult)">{{objResult.title}}</a>       
                  </div> 
                  <div>{{ "上映时间: " + objResult.year }}</div>
                  <el-button type="primary" class="pointer" @click="jump(objResult)" :loading=loadif>立即播放</el-button>   
            </div>           
    </div>
</template>

<script>
import {mapGetters} from "vuex"
export default {
    name:"list",
    props:["objResult"],
    mounted(){
        // this.imgUrl = require("https://pic0.iqiyipic.com/image/20200316/3b/a6/a_100364471_m_601_m6_260_360.jpg");
    },
    data(){
        return {
            imgUrl:"../assets/13.jpg",
            loadif:false,
            
        }
    },
    computed:{
         ...mapGetters(['result']),   


    },

    methods:{
       jump(obj){
            this.loadif = true; 
            //                   ("list");
            //                   (obj);
            this.$router.push({name:"final",params:{list:obj}})
       }
    }

}
</script>

